
/* header */
header{
    height: 100vh;
    background-color: #646464;
    background-image: url('../../aseete/image/hero-bg.jpg');
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-size: cover;
    background-position: center;
    background-blend-mode: multiply;
    color: #fff;
    text-align: center;
}
.navbar-container{
    padding: 20px 0;
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    background: rgba(0, 0, 0, 0.9);
} 
.navbar .nav-link{
    text-decoration: none;
    padding: 10px 15px;
    color: #fff;
}
.hero-title{
    font-size:3.5rem;
    padding-top: 350px;
}
.hero-desc{
    padding-top: 15px;
}